    max-width: 400px;
    margin: 0 auto;
}
.advantage_reason h4{
    font-size: 20px;
    color: #2e4a58;
    opacity: 1;
    font-weight: 700;
    margin: 16px 0 10px;
}
.inner_reason{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 40px 40px;
}
.advantage_reason .achis img{
    margin: auto;
    display: block;
    width: 100px;
}
.advantage_reason p {
    font-size: 15px;
}
/* contact_con */
.contact_con{
    text-align: center;
    padding: 50px;
}
.contact_con h3 {
    font-size: 30px;
    font-weight: normal;
    text-align: center;
    margin: 10px 0;
    padding: 0;
    text-transform: inherit;
}
.contact_con p {
    width: 100%;
    margin: 0 auto;
    font-size: 17px;
    max-width: 100%;
}
.contact_con p{
    width: 79%;
    margin: 0 auto;
    font-size: 17px;
}
.video-sec h1 {
    text-transform: uppercase;
    color: #fff;
    line-height: 1.3;
    font-weight: 600;
    position: absolute;
}
.video-sec h1 small {
    display: block;
    font-size: 160%;
    font-weight: normal;
    text-align: center;
}
/* home-perfect-plan */
#home-perfect-plan{
    background-color: #3A3A3A;
    background-position: center center;
    background-attachment: fixed;
    padding: 30px 0;
}
#home-perfect-plan .plans {
    background-position: center center;
    background-size: auto 100%;
    background-color: #0F0707;
}
#home-perfect-plan .inner {
    display: flex;
    align-items: center;
}
#home-perfect-plan [class*="col-"] {
    margin: 0 2px;
    flex: 1 1 20%;
    max-width: 20%;
}
.col-3 {
    display: inline-block;
    width: calc( 100% / 3 - 20.01px);
}
#home-perfect-plan .col-2, #home-perfect-plan .col-3, #home-perfect-plan .col-4 {
    background-color: #0F0707;
    height: 522px;
    position: relative;
    background-size: cover;
    background-position: center center;
    transition: all .3s ease;
}
#home-perfect-plan .col-2:hover:before, #home-perfect-plan .col-3:hover:before, #home-perfect-plan .col-4:hover:before {
    opacity: 0;
}
#home-perfect-plan .col-2:before, #home-perfect-plan .col-3:before, #home-perfect-plan .col-4:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: black;
    opacity: .5;
    transition: all .3s ease;
}
#home-perfect-plan .col-2 .perfect-cta, #home-perfect-plan .col-3 .perfect-cta, #home-perfect-plan .col-4 .perfect-cta {
    color: white;
    text-align: center;
    font-size: 55px;
    font-family: inherit;
    margin: 0;
    position: absolute;
    top: 0%;
    left: 50%;
    line-height: 1em;
    width: 100%;
    transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -o-transform: translateX(-50%);
}
#home-perfect-plan .col-2 .perfect-cta span, #home-perfect-plan .col-3 .perfect-cta span, #home-perfect-plan .col-4 .perfect-cta span {
    text-transform: uppercase;
}
#home-perfect-plan .col-2 .perfect-cta__compare, #home-perfect-plan .col-3 .perfect-cta__compare, #home-perfect-plan .col-4 .perfect-cta__compare {
    display: block;
    font-size: 35px;
    line-height: 1em;
    font-weight: 300;
    margin-top: 20px;
}
#home-perfect-plan .col-3 .perfect-cta__state, #home-perfect-plan .col-3 .perfect-cta__plans, #home-perfect-plan .col-4 .perfect-cta__state, #home-perfect-plan .col-4 .perfect-cta__plans {
    display: block;
}
#home-perfect-plan .col-2 .btn-cont, 
#home-perfect-plan .col-3 .btn-cont, 
#home-perfect-plan .col-4 .btn-cont {
    position: absolute;
    bottom: 5%;
    left: 0;
    width: 100%;
    padding: 0 15px;
}
#home-perfect-plan  .btn-cont .btn {
    width: 100%;
    margin: 0 auto;
    margin-bottom: 10px;
    font-size: 10px;
    padding: 10px 5px;
    display: block;
    white-space: normal;
    background-color: #6FA553;
    color: white;
    text-transform: uppercase;
    padding: 10px 10px;
    text-decoration: none;
    text-align: center;
    font-weight: bold;
    font-size: 11px;
}
#home-perfect-plan  .btn-cont .btn:hover{
    color: white;
    background-color: #64944a;
}
.col-2 {
    display: inline-block;
    width: calc( 100% / 2 - 20.01px);
}
.col-4 {
    display: inline-block;
    width: calc( 100% / 4 - 20.01px);
}
.perfect-cta span {
    display: block;
}

