.home_page{
    width: 100%;
}
.container{
    width: 80%;
    margin: 0 auto;
}
/* top-image */
.top_img img{
    height: 200px;
    background-position: center top;
    background-size: cover;
}
/* first_sec */
.first_sec{
    padding: 53px 0 40px;
}
.first_sec h1{
    font-size: 50px;
    font-weight: bold;
    margin-bottom: 0;
    text-align: center;
    text-transform: capitalize;
}
.first_sec h2{
    padding: 12px;
    font-size: 33px;
    text-align: center;
    text-transform: capitalize;
}
.first_sec p{
    font-size: 17px;
    line-height: 29px;
    text-align: center;
    color: #333;
}
/* home_wranty */
.home_wranty{
    text-align: justify;
    padding: 0px 50px;
    box-shadow: 0px 0px 14px -1px;
    margin: 1%;
    min-height: 480px;
    display: table;
}
.inner_homewranty{
    vertical-align: middle;
    display: table-cell;
}
.inner_homewranty h3{
    color: #00a1b8;
    font-size: 23px;
    font-weight: 600;
    text-transform: capitalize
}
.inner_homewranty p{
    font-size: 15px;
    color: #3A3A3A;
}
.inner_homewranty li{
    font-size: 14px;
    font-weight: 700;
    color: #727070;
    line-height: 22px;
}
/* acclaimed_apart */
.acclaimed_apart {
    width: 100%;
    height: auto;
    margin-top: 100px;
    background: linear-gradient(
180deg
, #e8e8e8 0%, #fff 45%, #fff 100%);
}
.acclaimed_apart h1{
    font-weight: normal;
    text-align: center;
    font-weight: 300;
    margin: 0;
    padding: 80px 0 20px 0;
    color: #3A3A3A;
    font-size: 48px;
}
.acclaimed_apart p{
    font-weight: normal;
    text-align: center;
    margin: 0 auto;
    font-size: 17px;
}
.acclaimed_reason{
    display: block;
    float: none;
    width: 50%;
    text-align: center;
    max-width: 400px;
    margin: 0 auto;
}
.acclaimed_reason h4{
    font-size: 20px;
    color: #2e4a58;
    opacity: 1;
    font-weight: 700;
    margin: 16px 0 10px;
}
.acclaimed_inner_reason{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    max-width: 1310px;
    padding: 38px 0px 0px;
    width: 100%;
    margin: 0 auto;
    position: relative;
}
.acclaimed_reason .achis img{
    margin: auto;
    display: block;
    width: 100px;
}
.acclaimed_reason p {
    font-size: 15px;
}
/* contact-us */
.contact_us{
    text-align: center;
    padding: 50px;
    background: linear-gradient(180deg, #e8e8e8 0%, #fff 45%, #fff 100%);
    margin-top: 4%;
    margin-bottom: 7%;
}
.contact_us h3{
    font-size: 30px;
    font-weight: normal;
    text-align: center;
    margin: 10px 0;
    text-transform: capitalize;
}
.contact_us p{
    width: 67%;
    margin: 0 auto;
    font-size: 16px;
    line-height: 29px;
    color: #333;
}